import { observer, useObservable } from "mobx-react-lite";
import React, { useRef } from "react";

const container = (data: any) => {
  return observer(
    (props: any, ref: any) => {
      let screenRef = useRef(null);
      let current = useObservable({
        name: ""
      });
      if (current.name === "") {
        Object.keys(data).map((r: any, index: number) => {
          if (index === 0) {
            current.name = r;
            screenRef.current = data[r].screen;
          }
        });
      }
      const navigation = {
        _routes: data,
        replace: function(screen: string) {
          if (data[screen]) {
            screenRef.current = data[screen].screen;
            current.name = screen;
          }
        },
        popToTop: function() {
          if (props.parentNavigation) {
            props.parentNavigation.popToTop();
          } else {
            let first = Object.keys(data)[0];
            current.name = first;
            screenRef.current = data[first].screen;
          }
        }
      };
      const Component: any = screenRef.current;
      if (ref && ref.current) {
        ref.current = navigation;
      }

      return <Component navigation={navigation} />;
    },
    { forwardRef: true }
  );
};

export default (data: any) => {
  return container(data);
};
